.VideoList .list-container{
   display: flex;
    border-bottom:1px solid #000000;
    background-color: rgb(146, 138, 138,0.5);
    -webkit-animation-name: listAnimation;
            animation-name: listAnimation;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
    cursor:pointer;
    transition:all 2s;
    flex-direction: row;
   flex-wrap:wrap;
    width:100%;
}
.VideoList{
    margin-top: 60px;
}
.VideoList .list-container .list-image-container{
    flex:20% 1;
    height:200px;

}
.VideoList .list-container .list-details-container{
    flex:60% 1;
     display: flex;
     flex-direction: column;
    align-content: center;
    height:200px;
}
.VideoList .list-container .details{
    padding:10px 20px;
    color:black;
    width:100%;
    font-size:16px;
    font-weight:800;

}
.VideoList .list-container:hover{
   -webkit-transform:translateX(20px);
           transform:translateX(20px)
}
@-webkit-keyframes listAnimation{
    0%{
        opacity:0;
    }
    100%{
        opacity:1;
    }
}
@keyframes listAnimation{
    0%{
        opacity:0;
    }
    100%{
        opacity:1;
    }
}
@media screen and (max-width:748px){
    .VideoList{
        margin-top: 60px;
    }
}
